<template>
  <Card class="card">
    <Breadcrumb>
      <BreadcrumbItem>船东中心</BreadcrumbItem>
      <BreadcrumbItem>修改船东信息</BreadcrumbItem>
    </Breadcrumb>
    <div class="box-container">
      <Collapse v-model="value1" accordion>
        <Panel name="1">
           <span>船东信息</span>
          <Row slot="content" class="row">
              <Col span="2">姓名</Col>
              <Col span="6">{{result.shipOwnerName ? result.shipOwnerName  :'-' }}</Col>
              <Col span="2">手机号</Col>
              <Col span="6">{{result.phone ? result.phone :'-'}}</Col>
              <Col span="2">船务公司</Col>
              <Col span="6">
                    <Input style="width:220px" placeholder="请输入船务公司" v-model="company"/>
              </Col>
          </Row>
           <Row slot="content" class="row">
              <Col span="2">船务公司网址</Col>
              <Col span="8"> <Input style="width:220px"  placeholder="请输入船务公司网址"  v-model="url" /></Col>
          </Row>
           <Row slot="content" class="row">
              <Col span="2">公司简介</Col>
              <Col span="20"> <Input style="width:220px" type="textarea" placeholder="请输入公司简介"  v-model="info"/>
              </Col>
          </Row>
          <Row slot="content" class="row">
            <Divider/>
          </Row>
           <Row slot="content" class="row">
              <Button type="primary" @click="save">保存</Button>
          </Row>
        </Panel>
      </Collapse>
    </div>
  </Card>
</template>
<script>
   export default {
        data () {
            return {
                value1: '1',
                value2:'2',
                result:'',
                company:'',
                url:'',
                info:""
            }
        },
        methods:{
          async  cdDetail(){
              let data  = {
                shipOwnerCode:this.$route.query.code
              }
             try{
                   let res = await this.$http.cdDetail(data)
                   if(0==res.data.status && res.data.result instanceof Object ){
                     this.result = res.data.result
                     this.company = res.data.result.companyName
                     this.url=res.data.result.companyOfficialUrl
                     this.info = res.data.result.info
                   }
              }
              catch(err){
                // this.$Notice.error({
                //   desc:'服务器错误,请稍后重试.'
                // })
                throw Error(err)

              }
           },
           async save(){
               let data = {
                   shipOwnerCode:this.$route.query.code,
                   companyName:this.company,
                   info:this.info,
                   companyOfficialUrl:this.url
               }
               try{

                   let res = await this.$http.updatecd(data)
                   if(!res.data.status){
                    this.$Notice.success({
                        desc:"保存成功"
                    })
                    setTimeout(()=>{
                        const {href} = this.$router.resolve({
                            path:'/cdCenter/cddetail',
                            query:{
                                code:this.$route.query.code
                            }
                         });
                         window.open(href, '_blank')
                    },1300)
                   }
                   else{
                        this.$Notice.error({
                        desc:res.data.msg
                    })
                    return false
                   }
               }
               catch(err){
                    this.$Notice.error({
                    desc:'服务器错误,请稍后重试.'
                })
                console.log(err)
               }
           }
        },
        created(){
          this.cdDetail()
        }
    }
</script>
<style lang="scss" scoped>
.table-row{
  margin: 20px 0px;
}
.ivu-row{
    padding: 0 30px;
    margin: 10px 0;
}
.table, tr ,td{
  border: 1px solid #e4e4ee;
  border-collapse: collapse;
  padding: 8px 30px;
  text-align: center;
}
</style>



